
.cartcontrol{
  font-size: 0;
}
.cartcontrol .cart-decrease{
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.cartcontrol .move-enter-active,.cartcontrol .move-leave-active{
  opacity: 1;
  transform: translate3d(0,0,0);
}
.cartcontrol .move-enter,.cartcontrol .move-leave-active{
  opacity: 0;
  transform: translate3d(24px,0,0);
}
.cartcontrol .inner{
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0,160,220);
  transition: all .4s linear;
}
.cartcontrol .move-enter-acive .inner,.cartcontrol .move-leave-active .inner{
  transform: rotate(0);
}
.cartcontrol .move-enter .inner,.cartcontrol .move-leave-active .inner{
  transform: rotate(180deg);
}
.cartcontrol .cart-count{
  display: inline-block;
  font-size: 10px;
  vertical-align: top;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  width: 12px;
  color: rgb(147,153,159);
}
.cartcontrol .cart-add{
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0,160,220);
}




