
.shopcart{
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 99;
  width: 100%;
  height: 48px;
}
.shopcart .content{
  display: -ms-flexbox;
  display: flex;
  background-color: #141d27;
}
.content .content-left{
  -ms-flex: 1;
      flex: 1;
  font-size: 0;
}
.content .content-right{
  -ms-flex: 0 0 105px;
      flex: 0 0 105px;
  width: 105px;
}
.content-left .logo-wrapper{
  display: inline-block;
  position: relative;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  top: -10px;
  margin: 0 12px;
  padding: 6px;
  background-color: #141d27;
}
.logo-wrapper .logo{
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.logo-wrapper .high-light{
  background: rgb(0,160,220);
}
.logo-wrapper .num{
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  text-align: center;
  line-height: 16px;
  border-radius: 16px;
  font-weight: 700;
  background: rgb(240,20,20);
  color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);
  font-size: 10px;
}
.logo .icon-shopping_cart{
  font-size: 24px;
  color: #80858a;
  line-height: 44px;
}
.high-light .icon-shopping_cart{
  color: #fff;
}
.content-left .price{
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  padding-right:12px;
  line-height: 24px;
  font-weight: 700;
  font-size: 16px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,.1);
  color:rgba(255,255,255,.4);
}
.content-left .high-light{
  color: #fff;
}
.content-left .desc{
  display: inline-block;
  vertical-align: top;
  margin:12px 0 0 12px;
  font-size: 10px;
  line-height: 24px;
  color:rgba(255,255,255,.4);
}
.content-right .pay{
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-size: 12px;
  font-weight: 700;
  background: #2b333b;
  color: rgba(255,255,255,.4);
}
.content-right .not-enough{
  background: #2b333b;
}
.content-right .enough{
  background: #00b43c;
  color: #fff;
}
.ball-container .ball{
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41);
}
.ball .inner{
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: rgb(0,160,220);
  transition: all .4s linear;
}
.shopcart-list{
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  transition: transform .4s;
  transform: translate3d(0,-100%,0);
}
.shopcart .fold-enter-active,.shopcart .fold-leave-active{
  transform: translate3d(0,-100%,0);
}
.shopcart .fold-enter,.shopcart .fold-leave-active{
  transform: translate3d(0,0,0);
}
.shopcart-list .list-header{
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.list-header .title{
  float: left;
  font-size: 14px;
  color: rgb(7,17,27);
}
.list-header .empty{
  float: right;
  font-size: 12px;
  color: rgb(0,160,220);
}
.list-content{
  padding: 0 18px;
  overflow: hidden;
  max-height: 217px;
  background: #fff;
}
.shopcart .food{
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.shopcart .food .name{
  line-height: 24px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.shopcart .food .price{
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(240,20,20);
}
.shopcart .food .cartcontrol-wrapper{
  right: 0;
  bottom: 6px;
}
.list-mask{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7,17,27,.6);
  -webkit-backdrop-filter:blur(10px);
          backdrop-filter:blur(10px);
  transition: all .4s;
}
.fade-mask-enter,.fade-mask-leave-active{
  opacity: 0;
}



