
.food{
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  background: #fff;
  width: 100%;
  transition: all .2s;
}
.move-enter-active,.move-leave-active{
  transform: translate3d(0,0,0);
}
.move-enter,.move-leave-active{
  transform: translate3d(100%,0,0);
}
.foot-content{
  /*position: relative;*/
}
.food-content .img-header{
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food-content .img-header img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-content .img-header .back{
  position: absolute;
  top: 0;
  left: 0;
}
.food-content .img-header .back .icon-arrow_lift{
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food-content .content{
  position: relative;
  padding: 18px;
}
.food-content .content .title{
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  font-weight: 700;
  color: rgb(7,17,27);
}
.food-content .content .food-detail{
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
  color: rgb(147,153,159);
}
.food-detail .sell-count{
  font-size: 10px;
}
.food-detail .rating{
  margin-left: 12px;
  font-size: 10px;
}
.food-content .content .price{
  font-weight: 700;
  line-height: 24px;
}
.content .price .now{
  margin-right: 8px;
  font-size: 14px;
  color: rgb(240,20,20);
}
.content .price .old{
  font-size: 10px;
  color: rgb(147,153,159);
  text-decoration: line-through;
}
.food-content .cartcontrol-wrapper{
  bottom: 12px;
  right: 12px;
}
.food-content .buy{
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: rgb(0,160,220);
  transition: opacity 0.4s;
}
.fade-buy-enter-active,.fade-buy-leave{
  opacity: 1;
}
.fade-buy-enter,.fade-buy-leave-active{
  opacity: 0;
}
.food .info{
  padding: 18px;
}
.food .info .title{
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.food .info .text{
  line-height: 24px;
  font-size: 12px;
  color: rgb(77,85,93);
  padding: 0 8px;
}
.rating{
  padding-top: 18px;
}
.rating .title{
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.rating-wrapper{
  padding: 0 18px;
}
.rating-wrapper .rating-item{
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.rating-item .user{
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.user .name{
  display: inline-block;
  vertical-align: top;
  margin-right: 6px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.user .avatar{
  display: inline-block;
  vertical-align: top;
  border-radius: 50%;
}
.rating-item .time{
  line-height: 12px;
  margin-bottom: 6px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.rating-item .text{
  line-height: 16px;
  font-size: 12px;
  color: rgb(7,17,27);
}
.rating-item .icon-thumb_down{
  margin-right: 4px;
  font-size: 12px;
  color:rgb(147,153,159);
  line-height: 16px;
}
.rating-item .icon-thumb_up{
  margin-right: 4px;
  font-size: 12px;
  color: rgb(0,160,220);
  line-height: 16px;
}
.no-rating{
  padding: 16px 0;
  font-size: 12px;
  color: rgb(147,153,159);
}



