
.ratingselect{
}
.rating-type{
  padding: 18px 0;
  margin: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,.1);
  font-size: 0;
}
.rating-type .block{
  display: inline-block;
  padding: 8px 12px;
  border-radius: 1px;
  margin-right: 8px;
  font-size: 12px;
  color: rgb(77,85,93);
}
.block.active{
  color: #fff;
}
.block .count{
  font-size: 8px;
  vertical-align: bottom;
}
.block.positive{
  background: rgba(0,160,220,.2);
}
.block.positive.active{
  background: rgb(0,160,220);
}
.block.negative{
  background: rgba(77,85,93,.2);
}
.block.negative.active{
  background: rgb(77,85,93);
}
.switch{
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 2px solid rgba(7,17,27,.1);
  color: rgb(147,153,159);
  font-size: 0;
}
.switch.on .icon-check_circle{
  color: #00c850;
}
.switch .icon-check_circle{
  margin-right: 4px;
  font-size: 24px;
}
.switch .text{
  font-size: 12px;
  vertical-align: top;
}



