
.goods{
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 46px;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}
.menu-wrapper{
  -ms-flex: 0 0 80px;
      flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.menu-wrapper .menu-item{
  display: table;
  height: 54px;
  width: 56px;
  padding: 0 12px;
  line-height: 14px;
}
.menu-item .icon{
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.menu-item .text{
  display: table-cell;
  position: relative;
  width: 56px;
  font-size: 12px;
  vertical-align: middle;
}
.menu-wrapper .current{
  position: relative;
  margin-top: -1px;
  z-index: 10;
  background: #fff;
}
.menu-wrapper .current .text{
  font-weight: 700;
  border:none;
}
.menu-wrapper .current .text:after{
  display: none;
}
.menu-item .text:after{
  display: block;
  left: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  content: "";
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.foods-wrapper{
  -ms-flex: 1;
      flex: 1;
}
.foods-wrapper .title{
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  background: #f3f5f7;
  color: rgb(147,153,159);
}
.foods-wrapper .food-item{
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food-item .icon{
  -ms-flex: 0 0 57px;
      flex: 0 0 57px;
  margin-right: 10px;
}
.foods-wrapper .food-item img{
  vertical-align: top;
}
.foods-wrapper .food-item:last-child{
  margin-bottom: 0;
  border-bottom: none;
}
.food-item .content{
  -ms-flex: 1;
      flex: 1;
  position: relative;
}
.food-item .content .name{
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: rgb(7,17,27);
}
.food-item .content .desc{
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.food-item .content .extra{
  font-size: 0;
  color: rgb(147,153,159);
}
.food-item .content .extra span{
  font-size: 10px;
}
.food-item .content .extra .count{
  font-size: 10px;
  margin-right: 12px;
}
.food-item .content .price{
  font-size: 0;
  line-height: 24px;
}
.food-item .content .price .now{
  font-weight: 700;
  margin-right: 8px;
  font-size: 14px;
  color: rgb(240,20,20);
}
.food-item .content .price .old{
  text-decoration: line-through;
  font-size: 10px;
  color: rgb(147,153,159);
}
.cartcontrol-wrapper{
  position: absolute;
  right: 0;
  bottom: -12px;
}
@media(-webkit-min-device-pixel-ratio:2){
.menu-item .decrease{
    background-image: url(decrease_3@2x.png);
}
.menu-item .discount{
    background-image: url(discount_3@2x.png);
}
.menu-item .guarantee{
    background-image: url(guarantee_3@2x.png);
}
.menu-item .invoice{
    background-image: url(invoice_3@2x.png);
}
.menu-item .special{
    background-image: url(special_3@2x.png);
}
}
@media(-webkit-min-device-pixel-ratio:3){
.menu-item .decrease{
    background-image: url(decrease_3@3x.png);
}
.menu-item .discount{
    background-image: url(discount_3@3x.png);
}
.menu-item .guarantee{
    background-image: url(guarantee_3@3x.png);
}
.menu-item .invoice{
    background-image: url(invoice_3@3x.png);
}
.menu-item .special{
    background-image: url(special_3@3x.png);
}
}



