
#app .tab {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 40px;
    position: relative;
    line-height: 40px;
    /*border-bottom: 1px solid rgba(7,17,27,.1);*/
}
.tab:after {
    display: block;
    left: 0;
    bottom: 0;
    position: absolute;
    width: 100%;
    content: '';
    border-top: 1px solid rgba(7, 17, 27, .1);
}
.tab .tab-item {
    -ms-flex: 1;
        flex: 1;
    text-align: center;
}
.tab .tab-item a {
    display: block;
    /*line-height: 14px;*/
    font-size: 14px;
    color: #778593;
}
.tab .tab-item a.active {
    color: rgb(240, 20, 20);
}



